
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.name-input {
    width: 20%;
    padding: 10px 18px;
    font-size: 18px;
    margin-bottom: 16px;
    border-radius: 5px;
}
.select-input {
    width: 20%;
    padding: 10px 18px;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    overflow: hidden;
}
.select-input select {
    padding: 5px 8px;
    width: 130%;
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    background: transparent;
    background-image: none;
    -webkit-appearance: none;
}
.select-input select:focus {
    outline: none;
}
.errors {
    width: 20%;
    padding: 10px 18px;
    font-size: 18px;
    margin-bottom: 16px;
    text-align: center;
    color: red;
}
.button {
    background-color: #4CAF50; /* Green */
    border: none;
    border-radius: 5px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}

.question-input {
  width: 60%;
  padding: 10px 18px;
  font-size: 18px;
  margin-bottom: 16px;
border-radius: 5px;
}
.question-item {
  text-align: right;
  width: 60%;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid darkslategray;
  border-radius: 5px;
  padding: 5px 18px;
  margin: 5px;
}
.question-text {
  text-align: left;
  font-size: 18px;
}
.question-author {
  text-align: right;
  font-size: 14px;
  color: darkslategray;
}
